@use "../_setup/vars.scss" as *;

.ReactModalPortal {
  position: relative;
  z-index: $z-index-modal-overlay;
}

.ReactModal__Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: $z-index-modal-overlay;
}

.ReactModal__Content {
  position: relative;
  z-index: $z-index-modal-overlay-1;
}

.section {
  background-color: transparent;
  width: 100vw;
  position: relative;
  top: 0;
  left: 0;
  z-index: 999;
  grid-template-columns: 3fr 2fr;

  .grid-item {
    width: 100%;

    &:first-child {
      background-color: white;
    }

    h2 {
      font-size: 5.09vw;
      text-align: center;
      margin: 0 5vw;
      margin-bottom: -4vw;
      line-height: 1.2;
    }
  }
}

.modal-header {
  background-color: $color-black;
  text-align: center;
  height: 8.05vh;
  min-height: 50px;
  position: fixed;
  grid-template-columns: 3fr 2fr;

  div {
    height: 8.05vh;
    display: flex;
    align-items: center;
    justify-content: center;

    &:first-child {
      width: 39.7vw;
    }

    &:last-child {
      width: 20.25vw;
    }
  }

  h3 {
    font-size: 2.31vw;
    font-weight: 700;
  }

  h4 {
    font-size: 2.31vw;
    font-weight: 300;
    background-color: $color-black;
    color: $color-white;
  }
}

.white {
  background-color: $color-white;
}

// Archive Wrapper
.archive_wrapper {
  width: 59.95vw;
  margin-top: clamp(30px, 8.05vh, 200px);
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  height: 91.95vh;

  &.grid {
    display: grid;
  }
}

// Single Archive
.single_archive {
  height: 100%;
  padding: 0 6.85vw;
  width: 100%;

  h3 {
    font-weight: 700;
    font-size: 1.157vw;
    line-height: 1.2;
    margin: 0 7px;
  }
}

// Image
.img {
  width: 100%;
  height: 46.82vh;

  &.red,
  .bg-red {
    background-color: #ff0000;
  }

  &.lightblue,
  .bg-lightblue {
    background-color: #00c2ff;
  }

  &.green,
  .bg-green {
    background-color: #70ff00;
  }

  &.blue,
  .bg-blue {
    background-color: #000aff;
  }
}

.red {
  background-color: #ff0000;
}

.lightblue {
  background-color: #00c2ff;
}

.green {
  background-color: #70ff00;
}

.blue {
  background-color: #000aff;
}

// Content Wrapper
.content_wrapper {
  display: grid;
  grid-template-columns: 3fr 2fr;
  row-gap: 50px;
}

.content_left {
  margin: 1.33vw;
  font-size: clamp(14px, 0.925vw, 20px);
  font-weight: 900;
}

.content_right {
  position: relative;

  a {
    border: 1px solid $color-black;
    border-radius: 50px;
    text-align: center;
    font-size: clamp(14px, 0.81vw, 16px);
  }
}

// Fixed Content
.fixed_content {
  position: fixed;
  bottom: 0;
  left: 39.7vw;
  width: 20.25vw;
  max-height: 42vh;

  p {
    padding: 0 20px;
    font-size: clamp(12px, 0.8vw, 12px);
    font-weight: 900;
  }

  a {
    display: block;
    margin: 2.238vh 30px;
    padding: 5px;
  }
}

// Section Links
.section {
  a {
    text-align: center;
    display: block;
    margin: 2.238vh 30px;
    font-size: clamp(18px, 1.157vw, 22px);
    font-weight: 800;
  }
}

// Text Red
.text_red {
  color: #ff0000;
}

// Mobile specific styles
@media (max-width: 767px) {
  .section {
    display: flex;
    flex-direction: column;

    div {
      width: 100%;
    }

    .a {
      text-align: center;
      display: block;
      margin: 2.238vh 0;
      font-size: clamp(18px, 1.157vw, 22px);
      font-weight: 800;

      &:last-child {
        margin-left: -20px;
      }
    }
  }

  .modal-header {
    display: flex;
    flex-direction: column-reverse;

    div {
      &:first-child {
        width: 100vw;
        background-color: #a0a0a0;
      }

      &:last-child {
        width: 100vw;
      }
    }
  }

  .archive_wrapper {
    width: 59.95vw;
    margin-top: 88px;
  }

  .img {
    height: 33vh;
  }

  .fixed_content {
    position: relative;
    left: 0;
    width: 100%;
  }

  .link-wrapper {
    margin: 0 6.85vw;
  }

  .content_wrapper {
    display: flex;
    flex-direction: column;
  }
}

// Living content (dynamic - controlled via className)
.living {
  &.show {
    display: block;
  }

  &.hide {
    display: none;
  }
}

.modal_wrapper {
  position: fixed;
  z-index: $z-index-modal;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto auto;
  min-width: 300px;
  width: clamp(300px, 90vw, 500px);
  max-height: 80vh;
  border: $border-width solid $color-black;
  border-radius: $radius-default;
  background: $color-bg-light;
  padding: $padding-md;

  @include sm {
    padding: $padding-sm;
  }

  h1 {
    margin: 0;
  }

  h3 {
    font-weight: 400;
    margin: $spacing-md 0;
  }

  @include mobile-sm {
    width: calc(100% - #{$spacing-xl});
    margin: auto $spacing-md;
    min-width: unset;
  }
}

.media_wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 35%;
}

.text_wrapper {
  height: 15%;

  h1 {
    text-align: center;
    border-bottom: 1px solid $color-border-gray;
    padding-bottom: $padding;
  }

  p {
    text-align: left;
  }
}

.title {
  height: 10%;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.content {
  width: 100%;
  height: 40%;
  position: relative;
  display: flex;
  align-items: flex-end;

  &::-webkit-scrollbar {
    display: none;
  }

  p {
    width: calc(100% - 10px);
    word-break: break-all;
    position: relative;
    padding: $padding;
    margin: 0;
  }

  @include tablet-lg {
    position: relative;
    width: 100%;
    margin-left: 0;
    border-left: none;
  }
}

.box {
  display: flex;
  border-top: 1px solid $color-border-gray;
  position: relative;
  width: 100%;
}

.item {
  background-color: $color-white;
  margin: $padding 3px;
  width: calc(20% - 6px);
  text-align: center;
}

.closeModal {
  text-align: center;
  cursor: pointer;
  margin-top: $padding;
}

.bottomContainer {
  position: fixed;
  bottom: 0;
  z-index: 11;
  display: inline-flex;
  justify-content: space-between;
  width: 100vw;

  &__left {
    display: inline-flex;
  }
}

.topContainer {
  position: fixed;
  top: 0;
  z-index: 11;
  display: inline-flex;
  justify-content: space-between;
  width: 100vw;
}
