@use "../_setup/vars.scss" as *;
@use "../_setup/fonts.scss" as *;
@use "../_setup/breakpoints.scss" as *;

.navigation-controls {
  bottom: $margin-corner;
  right: $margin-corner;
  position: fixed;
  z-index: $z-index-navigation-controls;
  display: flex;
  flex-direction: row;
  gap: 0;
  align-items: center;
  pointer-events: auto;

  @include mobile {
    bottom: 0;
    right: 0;
    width: 100vw;
    justify-content: flex-end;
  }

  @include aspect-ratio-1-1 {
    @include mobile;
  }
}

.navigation-controls-left {
  bottom: $margin-corner;
  left: $margin-corner;
  position: fixed;
  z-index: $z-index-modal-overlay;
  display: flex;
  flex-direction: row;
  gap: 0;
  align-items: center;
  pointer-events: auto;

  @include mobile {
    bottom: 0;
    right: 0;
    width: 100vw;
    justify-content: flex-end;
  }

  @include aspect-ratio-1-1 {
    @include mobile;
  }
}

.navigation-button {
  background-color: $color-mint;
  color: $color-black;
  text-transform: lowercase;
  border: none;
  cursor: pointer;
  transition: all $transi ease;
  white-space: nowrap;
  pointer-events: auto;
  position: relative;
  height: $margin-menu;
  max-height: $margin-menu;
  min-width: $margin-menu;
  padding: 0 $padding-s 2px $padding-s;
  border-radius: 0;
  @extend %ortica-regular;
  @include font-size-md;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }

  &.navigation-button-open {
    border-radius: $border-radius-lg;
  }
}

.navigation-button-pink {
  background-color: $color-pink-light;
  width: $margin-menu;
  height: $margin-menu;
  min-width: $margin-menu;
  max-width: $margin-menu;
  padding: 0;
  @extend %ortica-regular;
  @include font-size-md;
}

.navigation-button-inventory {
  background-color: $color-light-grey;
  color: $color-black;
  text-transform: uppercase;
  font-weight: 700;
  font-family: $font-family-ronzino;
  padding: 0 $padding;
}

.navigation-button-restart {
  background-color: $color-white;
  color: $color-black;
}

.navigation-button-rotation {
  background-color: $color-black;
  width: $margin-menu;
  height: $margin-menu;
  min-width: $margin-menu;
  max-width: $margin-menu;
  padding: 0;
  transition: opacity $transi ease;
  opacity: 1;
  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    width: 100%;
    height: 100%;
    display: block;
  }

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.navigation-button-submit {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: $padding-xs calc($margin-menu*1.6);
  white-space: normal;
  height: auto;
  min-height: $margin-menu;
  text-align: center;
  font-family: $font-family-ronzino;
  text-transform: uppercase;
  font-weight: 700;
  z-index: $z-index-navigation-controls;
  width: calc((#{$grid-column-width} * 3.33) + #{$margin});

  @include mobile {
  width: 100vw;
  }

  .submit-text {
    display: block;
    font-family: $font-family-ledger;
    font-size: $font-size-sm;
    font-weight: 400;
    line-height: 1.3;
  }

  .person-name {
    display: block;
    font-family: $font-family-ledger;
    font-size: $font-size-xs;
    font-weight: 400;
    margin-top: calc(#{$padding} * 0.25);
    opacity: 0.8;
    line-height: 1.2;
  }
}

.navigation-button-submit.navigation-button-default {
  background-color: $color-bg-light;
}

.navigation-button-submit.navigation-button-blue {
  background: $gradient-blue-white;
}

.navigation-button-submit.navigation-button-orange {
  background: $gradient-orange-dark;
}

.navigation-button-submit.navigation-button-mint {
  background: $gradient-mint-dark;
}

.navigation-button-submit.navigation-button-pink-gradient {
  background: $gradient-pink-white;
}

// Info Panel Styles (sliding from right)
.info-panel-overlay {
  display: none;
}

.info-panel-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  @include grid-width-with-margin(4);
  height: 100vh;
  background-color: $color-pink-light;
  z-index: $z-index-modal-overlay-1;
  transform: translateX(100%);
  transition: transform $transi ease;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  border: $border-width solid transparent;

  &:hover {
    border-color: $color-white;
  }

  &.open {
    transform: translateX(0);
  }

  @include mobile {
    @include grid-width-mobile-with-margin(4);
    width: 100vw;
    height: 50vh;
    top: auto;
    bottom: 0;
  }

  @include aspect-ratio-1-1 {
    @include mobile;
  }
}

.info-panel-close {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}

.info-panel-content {
  display: flex;
  flex-direction: column;
  gap: 0;
  flex: 1;
  margin-top: 0;
  padding-top: 0;
  align-items: flex-start;
  color: $color-black;
  margin: $margin-side;
  padding-right: $margin;

  h4 {
    margin: 0;
  }

}

// Submit Entry Panel Styles (sliding from right - exactly like info panel)
.submit-entry-panel-overlay {
  display: none;
}

.submit-entry-panel-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  @include grid-width-with-margin(4);
  height: 100vh;
  background-color: $color-black;
  z-index: $z-index-modal;
  transform: translateX(100%);
  transition: transform $transi ease;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  border: $border-width solid transparent;

  &:hover {
    border-color: $color-pink-light;
  }

  &.open {
    transform: translateX(0);
    z-index: 9999;
  }

  @include mobile {
    @include grid-width-mobile-with-margin(4);
    width: 100vw;
    height: 50vh;
    top: auto;
    bottom: 0;
  }

  @include aspect-ratio-1-1 {
    @include mobile;
  }
}

.submit-entry-panel-close {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 9999999;

  svg {
    line {
      stroke: $color-pink-light;
    }
  }
}

body.menu-open .corner-square.top-right {
  z-index: 9998;
}

.submit-entry-panel-content {
  display: flex;
  flex-direction: column;
  gap: 0;
  flex: 1;
  margin-top: 0;
  padding-top: 0;
  align-items: flex-start;
  color: $color-white;
  margin: $margin-side;
  padding-right: $margin;

  h5 {
    margin-top: 0;
  }

  .submit-entry-form {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: calc(#{$margin-side} * 4.4)
  }
}

.submit-entry-textarea,
.submit-entry-input {
  width: 100%;
  background-color: transparent;
  border: none;
  font-family: $font-family-roboto-flex;
  box-sizing: border-box;
  @include font-h2;
  letter-spacing: 0;
  line-height: 125%;
  font-weight: 200;
  font-style: italic;
  color: $color-white;
  margin: 0;
  padding-right: $margin-side;
  text-align: left;
  cursor: pointer;
  resize: vertical;
  &:focus {
    outline: none;
  }

  &::placeholder {
    @include font-h2;
    letter-spacing: 0 !important;
    line-height: 125%;
    font-weight: 200;
    font-style: italic;
    color: #BBBBBB;
    margin: 0;
    text-align: left;
    cursor: pointer;
    resize: vertical;
  }
}

.submit-entry-textarea {
  @include font-h2;
  letter-spacing: 0;
  line-height: 125%;
  font-weight: 200;
  font-style: italic;
  color: $color-white;
  margin: 0;
  margin-bottom: $margin-side;
  text-align: left;
  cursor: pointer;
  resize: vertical;
}

.submit-entry-interpretation {
  min-height: calc($wrapperHeight*0.86);
  max-height: calc($wrapperHeight*0.86);
}

.submit-entry-name,
.submit-entry-privacy-text {
  @include font-h3;
  letter-spacing: 0;
  line-height: 125%;
  font-weight: 200;
  font-style: italic;
  color: #BBBBBB;
  margin: 0;
  margin-bottom: $margin-side;
  text-align: left;
  padding:0;
  cursor: pointer;
  resize: vertical;
  p{
    margin: 0;
  }

  &::placeholder {
    @include font-h3;
    letter-spacing: 0;
    line-height: 125%;
    font-weight: 200;
    font-style: italic;
    color: $color-white;
    margin: 0;
    margin-bottom: $margin-side;
    text-align: left;
    cursor: pointer;
    resize: vertical;
  }
}

.submit-entry-privacy-text {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: $padding-xxs;
  cursor: initial !important;

  .submit-entry-privacy-content {

  }
}

.navigation-button-walk-through {
  background-color: $color-whitegray;
}

.navigation-button.navigation-button-open {
  background-color: $color-mint;
  border-radius: 0;
}